body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.main_container {
  margin: 2em;
}
.red_text {
  color: red;
}
.green_text {
  color: green;
}
.ticker-img {
  width: 16px;
  height: 16px;
}
.cursor-pointer {
  cursor: pointer;
}
.content-box {
  border: 1px lightgray solid;
  border-radius: 4px;
  width: 100%;
  padding: 1em;
}
.loading-animation-main {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.header {
  padding: 2em;
  padding-top: 4em;
  text-align: center;
}

.top-charts-table {
    border-right: 1px solid lightgray;
    width: 100%;
    height: 100%;
}
.table-index-coln {
    width: 0px;
}
.table-pagination {
    display: -webkit-inline-flex;
    display: inline-flex;
}
.coin-details-wrapper {
    padding-right: 1em;
    padding-top: 2em;
    padding-bottom: 2em;
}
.coin-details-chart-heading, .coin-details-details-heading {
    border-bottom: 2px lightgray solid;
    margin-bottom: 1em;
}
.coin-details-chart-heading {
    margin-bottom: 0.5em;
}
.coin-details-wrapper h2 {
    font-size: 2em;
}
.coin-details-wrapper h3 {
    font-size: 1.4em;
}
.coin-details-wrapper h4 {
    font-size: 1.2em;
    color: #ffa000; 
    font-weight: 400;
    line-height: 0.4;
}
.coin-details-wrapper p {
    line-height: 0.8;
}
.coin-details-wrapper .detail-label {
    font-weight: 500;
}
.simple-price-chart {
    margin-top: 1em;
    margin-bottom: 1em;
}
.coin-details-section {
    margin-top: 2em;
    margin-bottom: 1em;
}
.timeperiod-select {
    text-align: center;
}
.top-movers-table {
    width: 100%;
    height: 100%;
}
.latest-charts-container {
    padding: 1em;
}
.latest-charts-container h4 {
    margin-left: 0.2em;
}
.news-feed-main-container h4 {
    margin-top: 1em;
    margin-left: 1em;
    margin-bottom: 1em;
}
.news-feed-main-container p {
    margin-bottom: 0;
}
.news-item {
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}
.loading-animation-content-container {
    margin: 4em;
    text-align: center;
}
.loading-animation-news {
    display: inline-block;
}
.accent-text {
    color: #FFA000;
}
.bolded {
    font-weight: 500;
}
p {
    font-size: 1.2em;
}
a {
    font-weight: 400;
    color: #212121; 
}
a:hover {
    color: #424242;
    text-decoration: none;
}
.api-key-form-container {
    text-align: center;
    margin-top: 2em;
}
.api-form-submit {
    margin-top: 1em !important;
}
.api-key-form-container a {
    color: #FFA000;
}
